import {NgModule} from '@angular/core';
import {of, Observable} from 'rxjs';

import {IDiscoveryRequest, IDiscoveryApplication, APP_SWITCHER_SERVICE} from '@healthcatalyst/cashmere';

// prettier-ignore
// tslint:disable-next-line:max-line-length
const atlasIcon = 'data:image/png;base64,';

// prettier-ignore
// tslint:disable-next-line:max-line-length
const edwConsoleIcon = 'data:image/png;base64,';

// prettier-ignore
// tslint:disable-next-line:max-line-length
const ideaIcon = 'data:image/png;base64,';

const MockApplications: IDiscoveryApplication[] = [
    {
        ServiceName: 'Atlas4',
        Version: 1,
        DiscoveryServiceId: 13,
        ServiceUrl: '#',
        DiscoveryType: 'Application',
        Icon: atlasIcon,
        FriendlyName: 'Atlas',
        Description: 'A web application for interacting with the EDW',
        BuildNumber: null
    },
    {
        ServiceName: 'Operations Console',
        Version: 1,
        DiscoveryServiceId: 14,
        ServiceUrl: '#',
        DiscoveryType: 'Application',
        Icon: edwConsoleIcon,
        FriendlyName: 'Operations Console',
        Description: 'An administrative console for EDW administrators',
        BuildNumber: null
    },
    {
        ServiceName: 'IDEA',
        Version: 1,
        DiscoveryServiceId: 1014,
        ServiceUrl: '#',
        DiscoveryType: 'Application',
        Icon: ideaIcon,
        FriendlyName: 'IDEA',
        Description: 'A web application for collecting custom sets of data',
        BuildNumber: null
    }
];

class CustomAppSwitcherService {
    readonly allApplicationsUri = 'http://example.com/';

    getApplications(): Observable<IDiscoveryRequest> {
        return of({
            value: MockApplications
        });
    }
}

@NgModule({
    providers: [
        {
            provide: APP_SWITCHER_SERVICE,
            useClass: CustomAppSwitcherService
        }
    ]
})
export class AppSwitcherServiceModule {}
